nav .desktop-nav {
  background-color: var(--primary-black);
  z-index: 9999;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50px;
  padding: .5rem 2rem;
  display: none;
  position: fixed;
  top: 0%;
  left: 0%;
}

nav .logo {
  align-self: flex-start;
  width: 80px;
  height: 50px;
  margin-left: .5rem;
  overflow: hidden;
}

nav .logo img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

nav .desktop-nav .links {
  justify-content: center;
  align-self: center;
  gap: 3rem;
  height: 100%;
  margin-top: 1rem;
  display: flex;
}

nav .desktop-nav .links a {
  text-transform: uppercase;
  letter-spacing: .08em;
  color: var(--white);
  text-shadow: -.25px -.25px #0000, .25px .25px #0000;
  font-size: 1rem;
  line-height: 2rem;
  text-decoration: none;
  transition: all .25s ease-out;
  position: relative;
}

nav .desktop-nav .links a:hover {
  color: var(--primary-blue);
  text-shadow: -.25px -.25px 0 var(--primary-blue), .25px .25px var(--primary-blue);
}

nav .desktop-nav .links a:after {
  content: "";
  transform-origin: 100% 100%;
  background-color: #0087ca;
  width: 100%;
  height: 3px;
  transition: transform .25s ease-out;
  position: absolute;
  bottom: 15px;
  left: 0;
  transform: scaleX(0);
}

nav .desktop-nav .links a:hover:after {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

nav .mlh-flag {
  width: 100px;
  position: relative;
  top: 3.5rem;
  right: 2rem;
}

nav .mlh-flag img {
  width: 100%;
  height: 100%;
}

.show {
  display: block;
}

.hidden, nav .mobile-nav {
  display: none;
}

@media screen and (max-width: 1000px) {
  nav .mlh-flag {
    top: 0%;
    right: 10%;
  }

  nav .mlh-flag img {
    width: 100px;
  }

  nav .mobile-nav {
    transition: all .5s ease-in;
    display: block;
  }

  nav .mobile-nav-links {
    z-index: 1000;
    box-sizing: border-box;
    background-color: #00121f;
    border-radius: 10px;
    flex-direction: column;
    justify-content: center;
    width: 65%;
    height: 100%;
    padding: 20px;
    transition: all .5s ease-in;
    display: flex;
    position: fixed;
    right: 0;
    transform: translate(100%);
  }

  nav .mobile-nav-links a {
    color: var(--white);
    text-align: center;
    margin: 1.5rem 0;
    font-size: 1rem;
    font-weight: bold;
    text-decoration: none;
    transition: all .3s ease-in;
    position: relative;
  }

  nav .mobile-nav-links a:hover {
    color: var(--primary-blue);
    text-shadow: -.25px -.25px 0 var(--primary-blue), .25px .25px var(--primary-blue);
  }

  nav .mobile-nav-links a:after {
    content: "";
    transform-origin: 100% 100%;
    background-color: #969ea3;
    width: 100%;
    height: 2px;
    margin-top: 6px;
    transition: transform .5s ease-out;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
  }

  nav .mobile-nav-links a:hover:after {
    transform-origin: 0 100%;
    transition: transform .5s ease-out;
    transform: scaleX(1);
  }

  nav .mobile-nav-icons {
    color: var(--white);
    cursor: pointer;
    z-index: 1000;
    font-size: 2.5rem;
    position: fixed;
    top: 1%;
    right: 0%;
  }
}

@media screen and (min-width: 1000px) {
  .desktop-nav {
    display: flex !important;
  }
}

.mlh-flag2 {
  width: -moz-fit-content;
  width: fit-content;
  top: 0%;
}

.mlh-flag2 img {
  z-index: 500;
  width: 70px;
}

.burgMlh {
  margin-right: 1rem;
  display: flex;
}

